:root {
  --dark-color: #141414;
  --secondary-color: #3c948b;
  --crimson: crimson;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial;
}
.container {
  max-width: 1170px;
  margin: auto;
}
.btn {
  padding: 10px 17px;
  border: 1px solid var(--dark-color);
  background: var(--dark-color);
  margin: 1rem 0;
  font-size: 20px;
  color: crimson;
  cursor: pointer;
  transition: all 0.3s ease;
}
.btn:hover {
  color: var(--secondary-color);
}
.btn-block {
  display: block;
  width: 100%;
}
.loader img {
  display: block;
  margin: 2rem auto;
}
img {
  width: 210px;
  height: 295px;
  object-fit: cover;
}
/* NAVBAR */
.navbar {
  padding: 1rem;
  background: var(--dark-color);
}
.navbar__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__brand a {
  font-size: 1.4rem;
  color: var(--crimson);
  text-decoration: none;
  transition: all 0.3s ease;
}
.nav__brand a:hover {
  color: var(--secondary-color);
}
.nav__links {
  display: flex;
  list-style: none;
  align-items: center;
}

.nav__link a {
  display: block;
  padding: 1rem;
  color: var(--crimson);
  text-decoration: none;
  margin-left: 0.5rem;
  border: 1px solid var(--dark-color);
  transition: all 0.3s ease;
}
.nav__link a:hover {
  border-bottom: 1px solid var(--secondary-color);
  color: #3c948b;
}
/* NAVBAR END */
/* Searchbar */
.searchbar {
  width: 100%;
  margin: 2rem 0;
}
.searchbar__form > input {
  width: 100%;
  padding: 10px 17px;
  border: 1px solid var(--dark-color);
  background: lightgray;
  color: var(--dark-color);
  font-size: 20px;
  outline-width: 0;
}
.searchbar__form > input:focus {
  border: 1px solid var(--secondary-color);
  box-shadow: 0 0 1px 1px rgba(60, 148, 139, 0.5);
}
/* Searchbar End */
/* Alert Component */
.alert {
  padding: 10px 17px;
  font-size: 20px;
  margin: 1rem 0;
}
.alert-warning {
  background: #f8ffc4;
  border: 1px solid #ffee00;
}
.alert-danger {
  background: #ffc4c4;
  border: 1px solid #ff0000;
}
.alert-info {
  background: #d7eafe;
  border: 1px solid #0523b7;
}

/* List Item style */
.listitem {
  text-decoration: none;
  width: 210px;
  background: #fff;
  box-shadow: 0 0 1px 1px rgba(20, 23, 28, 0.1),
    0 3px 1px 0 rgba(20, 23, 28, 0.1);
  margin: 1rem auto;
  cursor: pointer;
  display: inline-block;
  transition: all 0.3s ease;
}
.listitem:hover {
  transform: scale(1.03);
}
.listitem__info {
  color: var(--dark-color);
  text-align: center;
}
.info__name {
  font-size: 1rem;
  padding: 5px;
}
.info__rating {
  color: var(--secondary-color);
  padding: 5px;
}
/* List Item style End */
/* Homepage Style */
.homepage__list {
  display: flex;
  flex-wrap: wrap;
}

/* Single Show Page Style */

.singleshow {
  display: flex;
  margin: 2rem 0;
}

.singleshow > img {
  margin-right: 2rem;
}

.singleshow__info p {
  margin: 1rem 0;
}

.singleshow__genre {
  display: inline-block;
  border-radius: 15px;
  margin: 0.5rem 1rem 0.5rem 0;
  padding: 10px 17px;
  background: var(--secondary-color);
  color: #fff;
}
/* About Page Style */

.about {
  margin: 2rem 0;
  width: 100%;
}

.about__title {
  font-size: 2rem;
  margin: 1rem;
}

.about__info {
  width: 100%;
  margin: auto;
}
.about__info > p {
  font-size: 1.4rem;
  margin: 1rem 0;
}

.about__info > div > ul > li {
  margin-left: 2rem;
  padding: 0.5rem;
}
